.blurBox{
    display: grid;
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(1,1,1,0.5);
    z-index: 11;
    place-items: center;
}

.searchBox{
    background-color: #392A48;
    position: absolute;
    z-index: 20;
    border-radius: 12px;
    width: 50%;
    height: 50%;
    border: 2px solid;
    border-color: rgba(0, 0, 0, 0.5), rgba(57,42,72, 1);

}
.searchBox h1{
    text-align: center;
    color: #f7f7ff;
    text-decoration: underline;


}
.searchBox h2{
    margin-top: 100px;
    color: #f7f7ff;
    text-align: center;
}
.searchBox select{
    border-radius: 6px;
    display: inline-block;
    width: 12vw;
    margin: 1vh;
    margin-top: 2vh;
    height: 2vh;
}
.searchBox input{
    display: inline;

    margin: 1vh;
    border-radius: 5px;
    height: 2vh;
    border-style: hidden;
}
.searchBox h5{
    text-align: left;
    margin: 10px;
    color: #f7f7ff;
}

.smallInput{
    width: 10.9vw;
}
.largeInput{
    width: 18vw;
}
.xbox {
    float: right;
    top: 0;
}
.submitButton {
    border: 1px solid black;

}

