h2 {
    color: #191919;
}
button {
    display: inline-block;
    #background-color: ;
    border-radius: 10px;
    font-size: 18px;
    #border: 4px double #191919;
    text-align: center;
    #color: #eeeeee;
    padding: 10px;
    width: 200px;
    transition: all 0.5s;
    margin: 10px;
}
button:hover {
    #box-shadow: 18px 18px 10px #191919;
    background-color: #17fc2f;
}


.displaybar {
    float: right;
    display: block;
    position: fixed;
    width: 336px;
    min-width: 300px;
    border: solid 2px black;
    #background: rgba(219,112,147,1);
    background-color: rgba(219,112,147,0.5);
    backdrop-filter: blur(9px) saturate(200%);
    -webkit-backdrop-filter: blur(9px) saturate(200%);
    top: 0;
    bottom: 0;
    right: 0;
    box-shadow: 0px 0px 10px 5px #191919;
    overflow: hidden;
    #filter: blur(10);
    #filter: opacity(1);
}
.displaycard{
    #height: 492px * 1.5;
    #width: 336px * 1.5;
    display: block;
    margin-left: -50px;
    margin-right: 0;
    margin-top: -100px;
    overflow: hidden;
    clip-path: polygon(0% 18%, 0% 70%, 100% 70%, 100% 18%);
    max-width : 131%;
}
.displaycardshadow{
    filter: drop-shadow(-1px 3px 3px #191919);
}

.namebar {
    align: center;
    text-align: center;
    background-color: #ffa;
    margin: 10px;
    border: solid 2px #191919;
    border-radius: 10px;
    box-shadow: 1px 2px 3px 2px #191919;
}
.statbar{
    margin-top: -150px;
}
.atkbar {
    display: inline-grid;
    text-align: center;
    background-color: #ffa;
    #margin: 10px;
    border: solid 2px #191919;
    border-radius: 10px;
    box-shadow: 1px 2px 3px 2px #191919;
    margin-right: 30px;
}
.defbar {
    display: inline-grid;
    text-align: center;
    background-color: #ffa;
    #margin: 10px;
    border: solid 2px #191919;
    border-radius: 10px;
    box-shadow: 1px 2px 3px 2px #191919;
    margin-left: 30px;
}
.lvlbar {
    display: inline-block;
    align: left;
    width: 50px;
    margin-left: 5px;
    align-content: left;
    #margin-left: -75px;
    float: left;
}
.typebar {
    display: inline-block;
    align: center;
}
.attrbar {
    display: inline-block;
    align: right;
    #margin-left: 75px;
    float: right;
}
.descbar {
    background-color: #ffa;
    display: flex;
    border: solid 2px black;
    border-radius: 15px;
    padding: 10px;
    margin: 10px;
    box-shadow: 1px 2px 3px 2px #191919;

}

.pricelist{
    display: flex;
    flex-direction: row;
}

.priceblock{
    border: 1px solid black;
    background-color: gray;
    width : 9vw;
}


/* experimental */

.tinydeck {
    display: grid;
    grid-gap: 1px;
    grid-row: 5;
    grid-column: 8;
}
.tinycard {
    width: 63px;
    height: 90px;
}
