:root {
  font-size: 16px;
  font-family: 'Open Sans';
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #191919;
  --bg-secondary: #141418;
  --transition-speed: 600ms;
}

.navbar::-webkit-scrollbar {
  width: 0;
}

.navbar {
  position: fixed;
  background-color: var(--bg-primary);
  transition: width 600ms ease;
  overflow: scroll;
  z-index: 10;
  transition: 0.3s ease-in;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 20vw;

}
.navbar-open {
transform: translate(0);
}
.navbar-closed {
    transform: translate(-13vw);
}

.nav-item {
  display: flex;
  width: 100%;
  margin-top: 10px;
  justify-content: space-between;
}

.nav-item p {
    color: white;
    display: inline;
    left: 0;
    float:left;
    margin-left: 30px;
    margin-top: 30px;
}


.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: var(--text-primary);
  text-decoration: none;
  filter: grayscale(100%) opacity(0.7);
  transition: var(--transition-speed);
}

.nav-item:hover {
 filter: grayscale(0%) opacity(1);
 background: var(--bg-secondary);
 color: var(--text-secondary);
}

.link-text {
  display: none;
  margin-left: 1rem;
}

.nav-link svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.fa-primary {
  color: #f7f7ff;
}

.fa-secondary {
  color: #f7f7ff;
}

.fa-primary,
.fa-secondary {
  transition: var(--transition-speed);
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: var(--text-secondary);
  background: var(--bg-secondary);
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  transform: rotate(0deg);
  transition: var(--transition-speed);
}

.logo-text{
  display: inline;
  position: absolute;
  left: -999px;
  transition: var(--transition-speed);
}

.navbar:hover .logo svg {
  transform: rotate(-180deg);
}
.icon-button{
    margin-right: 1vw;
}
.chev{
    transition: rotate(180deg);
}
.chev:active{
    transition: rotate(180deg);
}
