.disablescroll::-webkit-scrollbar {
  display: none;
}
.disablescroll {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
.background {
    position: absolute;
    display: block;
    #background-color: #1c4c64;
    background-color: #5d5b5b;
    #animation: backgroundAnim ease-in 10s;
    background-image :
        radial-gradient(at 47% 33%, rgb(39,154,241) 0, transparent 59%), radial-gradient(at 82% 65%, hsl(217.63, 65%, 37%) 0, transparent 55%);
    #animation-iteration-count: infinite;

    height: 100%;
    margin: 0;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 10;
    overflow: scroll;
}
@keyframes backgroundAnim2 {
    0%{
        background-color: #ffffff;
    }
    50%{
        background-color: #000000;
    }
    100%{
        background-color: #ffffff;
    }

}
@keyframes backgroundAnim {
    0% {
        background-image:
            radial-gradient(at 47% 33%, rgb(39,154,241) 0, transparent 59%),
            radial-gradient(at 82% 65%, hsl(217.63, 65%, 37%) 0, transparent 55%);
    }
    50% {
        background-image:
            radial-gradient(at 47% 33%, rgb(39,154,241) 0, transparent 59%),
            radial-gradient(at 82% 65%, hsl(217.63, 65%, 37%) 0, transparent 55%);
    }
    100%{
        background-image:
            radial-gradient(at 47% 33%, rgb(39,154,241) 0, transparent 59%),
            radial-gradient(at 82% 65%, hsl(217.63, 65%, 37%) 0, transparent 55%);
    }

}

