.maincontainer {
    position: relative;
    display: inline-block;
    margin: 1rem;
    height: 185px;
    width: 126px;

}
.thecard {
    position: absolute;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    #transform: translate(-1000px);
    transition: all 0.8s cubic-bezier(0.21, -0.43, 0.72, 1.49);
    animation-fill-mode: forwards;
    animation: deckLayer 2s  ease 4s forwards;
    animation-duration: 4s;
    animation-delay: -1s;
    visibility: hidden;
}
/*.thecard:hover {
    transform: rotateY(180deg);
}*/
.thefront {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    color: orange;
}
.thefront img {
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    user-drag: none;
}
.thefront:hover {
    box-shadow: 1px 7px 14px rgba(0,0,0,0.5), 0 5px 5px rgba(0,0,0,0.25);
    transform: scale(1.1);
    transition: 0.6s
}
.theback {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    background-image: image('./img/cardback.png');
    transform: rotateY(180deg);
}

.card {
	background: #fff;
	border-radius: 2px;
	display: inline-block;
	height: 185px; 	/* 246px */
	width: 126px;	/* 168px */

	position: relative;
	box-shadow: 0 1px 3px 6px rgba(0,0,0,0.12), 0 1px 2px  rgba(0,0,0,0.24);
	transition: all 1s cubic-bezier(.25,.8,.25,1);
	z-index: 2;
}
.aaaaacard:hover {
	box-shadow: 1px 7px 14px rgba(0,0,0,0.5), 0 5px 5px rgba(0,0,0,0.25);
    transform: scale(1.1);
    transition: 0.6s
}

@keyframes deckLayer {
    0% {
        transform: translate(-1000px) rotateY(180deg);
    }
    10%{
        visibility: visible;
    }
    70%{
        transform: translate(0px) rotateY(180deg);
    }
    100%{
        transform: rotateY(0deg) translate(0px);
        visibility: visible;
    }
}
